* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
	background-color: #f7f7f7;
	padding: 40px 20px;
}

h1 {
	text-align: center;
	margin-bottom: 40px;
	color: #333;
}
.gallery {
	width: 200px;
}
.layout-section {
	margin-bottom: 80px;
}

.layout-title {
	margin-bottom: 20px;
	padding-bottom: 10px;
	border-bottom: 2px solid #e0e0e0;
	color: #333;
}

/* Layout 1: Modern Card Grid */
.team-grid-1 {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
	gap: 30px;
}

.team-member-1 {
	background-color: white;
	border-radius: 12px;
	overflow: hidden;
	box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
	transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.team-member-1:hover {
	transform: translateY(-10px);
	box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
}

.member-image-1 {
	width: 100%;
	height: 280px;
	object-fit: cover;
}

.member-info-1 {
	padding: 20px;
}

.member-name-1 {
	font-size: 22px;
	margin-bottom: 8px;
	color: #333;
}

.member-role-1 {
	color: #666;
	font-size: 16px;
	margin-bottom: 15px;
}

.member-bio-1 {
	color: #666;
	font-size: 15px;
	line-height: 1.6;
	margin-bottom: 20px;
}

.social-links-1 {
	display: flex;
	gap: 15px;
}

.social-link-1 {
	color: #333;
	text-decoration: none;
	font-size: 20px;
	transition: color 0.3s ease;
}

.social-link-1:hover {
	color: #3498db;
}